.cart {
  padding: 21px;
  max-width: 800px;
  margin: 0 auto;
}

.cart h2 {
  text-align: center;
}

.cart-item {
  display: flex;
  align-items: center;
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

.cart-item img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.cart h3 {
  text-align: right;
  margin-top: 20px;
  font-size: 20px;
}
